import * as React from 'react';
import Button from '@mui/material/Button';


export default function PrimaryButton({width,
    height,
    backgroundColor,
    color,
    border,
    borderRadius,
    fontSize,
    buttonText,
    className,
    variant,
margin,
endIcon}) {
     
      return (
          <Button variant={variant} endIcon={endIcon} className={className} color="primary" style={{width, height, backgroundColor, color, border, borderRadius, fontSize , textTransform:"capitalize" , margin}}>
             {buttonText}
          </Button>
      )
  }